import React, { useState, useEffect } from 'react';
import {
  CModal,
  CModalHeader,
  CModalTitle,
  CModalBody,
  CModalFooter,
  CButton,
  CRow,
  CCol
} from '@coreui/react';
import {
  Title,
  Text,
  Color
} from '../components/TextsComponents';
import Card from '../components/CardCompany/CardCompany';
import Cards from '../components/CardsCompany/CardsCompany';
import Faixa from '../components/FaixaHorarios/Horarios';

import { apiFetchGet } from '../helpers/helper';

const CompanyComponent = ({ open, setOpen }) => {
  const [horarios, setHorarios] = useState([]);
  const [funcionamento, setFuncionamento] = useState(false);
  const [empresaAtual, setEmpresa] = useState({});
  const [formasPagamento, setFormasPagamento] = useState([]);
  const [idEmpresa, setIdEmpresa] = useState();

  useEffect(() => {
    if (open) setIdEmpresa(localStorage.getItem('id_empresa'));
  }, [open]);

  useEffect(() => {
    if (idEmpresa) {
      fetchHorarios();
      companyData();
    }
  }, [idEmpresa]);

  async function fetchHorarios() {
    const response = await apiFetchGet(`/horariosVisible/${idEmpresa}`);
    setHorarios({ horarios_func: response.data.rows });
    setFuncionamento(response.data.horario_funcionamento);
  };

  async function companyData() {
    const response = await apiFetchGet(`empresasVisible/single/${idEmpresa}`);
    const {
      cart_alelo,
      cart_american_express,
      cart_amex,
      cart_cabal,
      cart_diners_club,
      cart_elo,
      cart_hipercard,
      cart_master_card,
      cart_sodexo,
      cart_ticket,
      cart_visa,
      cart_vr,
      ...rest
    } = response.data;

    const formas = {
      cart_alelo,
      cart_american_express,
      cart_amex,
      cart_cabal,
      cart_diners_club,
      cart_elo,
      cart_hipercard,
      cart_master_card,
      cart_sodexo,
      cart_ticket,
      cart_visa,
      cart_vr
    };

    setFormasPagamento(formas);
    setEmpresa(rest);
  };

  return (
    <CModal
      show={open}
      onClose={setOpen}
    >
      <CModalHeader closeButton>
        <CModalTitle>Detalhes do Restaurante</CModalTitle>
      </CModalHeader>
      <CModalBody>
        <CRow>
          <CCol xs="12" lg="12">
            <Card>
              <Title>
                <Color active={empresaAtual.ativo} text={empresaAtual.ativo ? 'Status - Online' : 'Status - Ofline'} />
              </Title>
              <Text>
                <Color active={funcionamento} text={funcionamento ? 'Em horário de funcionamento' : 'Fora de horário de funcionamento'} />
              </Text>
            </Card>
          </CCol>
          <CCol xs="12" lg="12">
            <Card>
              <Title>Cartões</Title>
              <CRow>
                {
                  Object.keys(formasPagamento).map((item, index) => (
                    <CCol xs="6" lg="4" key={index}>
                      <Cards active={formasPagamento[item]} text={item.replace('cart_', '').replace('_', '')} />
                    </CCol>
                  ))
                }
              </CRow>
            </Card>
          </CCol>
          <CCol xs="12" lg="12">
            <Card>
              <Title>Informações</Title>
              <Text><b>Razão Social: </b>{empresaAtual.razao_social}</Text>
              <Text><b>Nome Fantasia: </b>{empresaAtual.nome_fantasia}</Text>
              <Text><b>CNPJ: </b>{empresaAtual.cnpj}</Text>
              <Text><b>Celular: </b>{empresaAtual.telefone_empresa}</Text>
              <Text><b>Fixo: </b>{empresaAtual.telefone_fixo}</Text>
              <Text><b>Gerente Diurno: </b>{empresaAtual.gerente_diurno}</Text>
              <Text><b>Gerente Noturno: </b>{empresaAtual.gerente_noturno}</Text>
              <Text><b>Endereço: </b>{empresaAtual.endereco}, n°{empresaAtual.numero}</Text>
              <Text><b>Bairro: </b>{empresaAtual.bairro}</Text>
              <Text><b>Cidade: </b>{empresaAtual.cidade}, {empresaAtual.uf}</Text>
              <Text><b>Cep: </b>{empresaAtual.cep}</Text>
              <Text><b>Email: </b>{empresaAtual.email_envios}</Text>
            </Card>
          </CCol>
          <CCol xs="12" lg="12">
            <Card>
              <Faixa numero={1} form={horarios} desativado />
            </Card>
          </CCol>
          <CCol xs="12" lg="12">
            <Card>
              <Faixa numero={2} form={horarios} desativado />
            </Card>
          </CCol>
          <CCol xs="12" lg="12">
            <Card>
              <Faixa numero={3} form={horarios} desativado />
            </Card>
          </CCol>
        </CRow>
      </CModalBody>
      <CModalFooter>
        <CButton
          onClick={() => setOpen(false)}
          block
          color="danger"
          style={{ width: '100%' }}
        >
          Fechar
        </CButton>
      </CModalFooter>
    </CModal>
  );
};

export default CompanyComponent;
