import React from 'react';

const CardsComponent = ({ active, text }) => {

  return (
    <div
      style={{
        padding: '5px',
        borderRadius: '3px',
        backgroundColor: active ? 'green' : 'red',
        color: 'white',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        fontSize: '12px',
        fontWeight: 'bold',
        marginBottom: '5px'
      }}
    >
      {text}
    </div>
  );
};

export default CardsComponent;
