import React, { useState, useEffect } from 'react';
import {
  CDropdown,
  CDropdownToggle
} from '@coreui/react';
import CIcon from '@coreui/icons-react';
import ModalCompany from './ModalCompany';

const TheHeaderDropdownCompany = () => {
  const [open, setOpen] = useState(false);

  return (
    <>
      <CDropdown
        inNav
        className="c-header-nav-item mx-2"
      >
        <CDropdownToggle className="c-header-nav-link" caret={false} onClick={() => setOpen(true)}>
          <CIcon name="cil-building" />
        </CDropdownToggle>
      </CDropdown>
      <ModalCompany {...{ open, setOpen }} />
    </>
  )
}

export default TheHeaderDropdownCompany;
