import React, { useState } from 'react';

import {
  CRow,
  CCol,
  CInput,
  CInputCheckbox,
  CFormGroup
} from '@coreui/react';

import { Label } from '../TextsComponents';

export default function Faixa({ setForm, form, numero, editavel, desativado, opened = false }) {

  const [visible, setVisible] = useState(opened);

  const handleInput = (key) => (evt) => {
    handleChange(key, evt.target.value);
  }

  const handleCheckbox = (key) => (evt) => {
    const value = evt.target.checked ? true : false;
    handleChange(key, value);
  }

  const handleChange = (key, value) => {
    setForm(
      {
        ...form,
        horarios_func: {
          ...form.horarios_func,
          [`${formatText(key)}`]: value
        }
      }
    );
  }

  const faixa = `faixa_${numero}`;

  const formatText = (key) => key ? `${key}_${faixa}` : faixa;

  function getValue(key) {
    if (form.horarios_func && form.horarios_func[`${formatText(key)}`])
      return form.horarios_func[`${formatText(key)}`];
    return '';
  }

  const days = ['seg', 'ter', 'qua', 'qui', 'sex', 'sab', 'dom'];

  function getDayName(day) {
    switch (day) {
      case 'seg':
        return 'Segunda';
      case 'ter':
        return 'Terça';
      case 'qua':
        return 'Quarta';
      case 'qui':
        return 'Quinta';
      case 'sex':
        return 'Sexta';
      case 'sab':
        return 'Sábado';
      case 'dom':
        return 'Domingo';
    }
  }

  function renderWeekDays() {
    return days.map((day, index) => (
      <CCol xs="6" lg="6" key={index} style={{ marginTop: '5px' }}>
        {getDayName(day)}{'  '}
        <CInputCheckbox
          onChange={handleCheckbox(`acesso_${day}`)}
          checked={getValue(`acesso_${day}`)}
          readOnly={desativado}
          style={{ marginLeft: '0.5rem' }}
        />
        <br></br><br></br>
        <CFormGroup>
          <label for={`inputInit${index}`}>Inicial</label>
          <CInput
            onChange={handleInput(`hora_fun_ini_${day}`)}
            value={getValue(`hora_fun_ini_${day}`)}
            type="time"
            readOnly={desativado}
            id={`inputInit${index}`}
          />
        </CFormGroup>
        <br></br><br></br>
        <CFormGroup>
          <label for={`inputFinal${index}`}>Final</label>
          <CInput
            onChange={handleInput(`hora_fun_fim_${day}`)}
            value={getValue(`hora_fun_fim_${day}`)}
            id="time"
            label="Final"
            type="time"
            readOnly={desativado}
            id={`inputFinal${index}`}
          />
        </CFormGroup>
      </CCol>
    ))
  }

  return (
    <CRow>
      <CCol xs="12" lg="12" style={{ alignItems: 'center' }}>
        <div onClick={() => setVisible(!visible)}>
          <Label text={`Faixa Horário ${numero}`} />
        </div>
        {
          editavel &&
          <CInputCheckbox
            type="checkbox"
            onChange={handleCheckbox()}
            checked={getValue()}
            readOnly={desativado}
          />
        }
      </CCol>
      <CCol xs="12" lg="12">
        <CRow>
          {visible && renderWeekDays()}
        </CRow>
      </CCol>
    </CRow>
  )
}
