import React from 'react';

const LabelBoxComponent = ({ active, text }) => {

  return (
    <div
      style={{
        padding: '5px',
        color: 'white',
        backgroundColor: active ? 'green' : 'red',
        fontSize: '14px',
        fontWeight: 'bold',
        borderRadius: '3px'
      }}
    >
      {text}
    </div>
  );
};

export default LabelBoxComponent;
