import React from 'react';

const CardCompanyComponent = ({ children }) => {

  return (
    <div style={{
      width: '100%',
      padding: '15px',
      display: 'flex',
      flexDirection: 'column',
      border: '1px solid rgba(0, 0, 0, .2)',
      marginBottom: '10px'
    }}>{children}</div>
  );
};

export default CardCompanyComponent;
