import solutek from 'axios';
import axios from '../config/GlobalConfig';
import Swal from 'sweetalert2';

export const apiFetchGet = async (endpoint, body = null) => {
  const response = await axios.get(`${endpoint}`, {
    params: body
  });

  return response;
};

export const apiFecthPost = async (endpoint, body) => {
  const response = await axios.post(`${endpoint}`, body);

  return response;
};

export const apiFetchWhatsApp = async (body) => {
  const response = await solutek.post(`https://www.solutek.online/api/whatsapp/gateway/json/enviar`, body, {
    headers: {
      'content-type': 'application/x-www-form-urlencoded'
    }
  });

  if (!response.data)
    Swal.fire({
      icon: 'warning',
      title: 'Atenção...',
      text: 'Ocorreu um erro ao enviar as informações para o whatsapp!',
      customClass: {
        container: 'test'
      },
    });
};
