import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';

import {
    CModal,
    CModalHeader,
    CModalTitle,
    CModalBody,
    CModalFooter,
    CButton,
    CRow,
    CCol
} from '@coreui/react';
import CIcon from '@coreui/icons-react';
import { Title, Text, Label } from '../components/TextsComponents';

import { removeToCartAction } from '../store/cart/actions';
import { addFormAction } from '../store/form/actions';
import { formSelector } from '../store/form/selectors';

const ModalCartComponent = ({ open, setOpen }) => {
    const history = useHistory();
    const dispatch = useDispatch();
    const form = useSelector(formSelector);

    const { produtos_pedidos, taxa_de_entrega } = form;

    const [porcao, setPorcao] = useState(false);
    const [adicionaisEscolhidos, setAdicionaisEscolhidos] = useState([]);
    const [id, setId] = useState();
    const [sub_id, setSubId] = useState();

    const increment = (item) => {
        let produtos_pedidos = [];

        if (item.info === 'adicionais' && !item.sub_id)
            produtos_pedidos = incrementaAdicionalSemVinculo(item.id);
        else
            produtos_pedidos = incrementaAdicionalOuProdutoComVinculo(item.id, item.sub_id);

        dispatch(addFormAction({
            ...form,
            produtos_pedidos
        }));
    }

    const incrementaAdicionalSemVinculo = (id) => {
        const produtos_pedidos = form.produtos_pedidos.map(adicional => {
            if (adicional.id === id && !adicional.sub_id)
                return { ...adicional, quantity: adicional.quantity + 1 };
            return adicional;
        })

        return produtos_pedidos;
    }

    const incrementaAdicionalOuProdutoComVinculo = (id, sub_id) => {
        const produtos_pedidos = form.produtos_pedidos.map(adicional => {
            if (adicional.sub_id === sub_id && adicional.id === id)
                return { ...adicional, quantity: adicional.quantity + 1 };
            return adicional;
        })

        return produtos_pedidos;
    }

    const decrement = (item) => {
        let produtos_pedidos = [];

        if (item.quantity === 1)
            return handleRemoveItemCart(item);
        if (item.info === 'adicionais' && !item.sub_id)
            produtos_pedidos = decrementaAdicionalSemVinculo(item.id);
        else
            produtos_pedidos = decrementaAdicionalOuProdutoComVinculo(item.id, item.sub_id);

        dispatch(addFormAction({
            ...form,
            produtos_pedidos
        }));
    }

    const decrementaAdicionalSemVinculo = (id) => {
        const produtos_pedidos = form.produtos_pedidos.map(adicional => {
            if (adicional.id === id && !adicional.sub_id)
                return { ...adicional, quantity: adicional.quantity - 1 }
            return adicional
        })

        return produtos_pedidos
    }

    const decrementaAdicionalOuProdutoComVinculo = (id, sub_id) => {
        const produtos_pedidos = form.produtos_pedidos.map(adicional => {
            if (adicional.sub_id === sub_id && adicional.id === id)
                return { ...adicional, quantity: adicional.quantity - 1 }
            return adicional
        })

        return produtos_pedidos;
    }

    const handleRemoveItemCart = (item) => {

        let produtos_pedidos = [];

        if (item.info === 'adicionais' && !item.sub_id)
            produtos_pedidos = removerAdicionalPorId(item.id)
        else if (item.info === 'adicionais' && item.sub_id)
            produtos_pedidos = removerAdicionalPorSubId(item.id, item.sub_id)
        else
            produtos_pedidos = removerPorcaoPorSubId(item.sub_id)

        dispatch(addFormAction({
            ...form,
            produtos_pedidos
        }));
        dispatch(removeToCartAction());
    }

    const removerAdicionalPorId = (id) => {
        const produtos_pedidos = form.produtos_pedidos.filter(adicional => adicional.id !== id || adicional.sub_id)

        return produtos_pedidos
    }

    const removerAdicionalPorSubId = (id, sub_id) => {
        const produtos_pedidos = form.produtos_pedidos.filter(adicional => adicional.sub_id !== sub_id || adicional.id !== id)

        return produtos_pedidos
    }

    const removerPorcaoPorSubId = (sub_id) => {
        const produtos_pedidos = form.produtos_pedidos.filter(produto => produto.sub_id !== sub_id)

        return produtos_pedidos
    }

    function getSubTotal() {
        const total = produtos_pedidos.reduce((soma, { quantity, valor, sub_id, info, avulso }) => {
            if (avulso)
                return soma + quantity * valor;

            if (info === 'adicionais')
                return soma + 0;

            const reduced = produtos_pedidos.reduce((soma, adicional) => {
                if (adicional.sub_id === sub_id && adicional.info === 'adicionais')
                    return soma + (adicional.valor * adicional.quantity);

                return soma + 0;
            }, 0);

            return soma + (reduced + valor) * quantity;
        }, 0);

        return total;
    }

    function getTotal() {
        return getSubTotal() + (taxa_de_entrega ?? 0);
    }

    function getAdicionaisEscolhidos(sub_id) {
        const adicionaisEscolhidos = produtos_pedidos.filter(produto => {
            if (produto.info === 'adicionais' && sub_id === produto.sub_id)
                return produto;
        });

        setAdicionaisEscolhidos(adicionaisEscolhidos);
    }

    function toFinishCart() {
        setOpen(false);
        history.push('/finishcart')
    };

    return (
        <CModal
            show={open}
            onClose={setOpen}
        >
            <CModalHeader closeButton>
                <CModalTitle>Carrinho</CModalTitle>
            </CModalHeader>
            <CModalBody>
                <CRow>
                    {
                        produtos_pedidos.length === 0 ?
                            <CCol xs="12" lg="12" style={{ marginBottom: '20px' }}>
                                <CRow style={{ textAlign: 'center', height: '100%', alignItems: 'center' }}>
                                    <CCol xs="12" lg="12">
                                        <strong>
                                            <text style={{ fontSize: '16px' }}>Nenhum produto encontrado </text>
                                            <CIcon name="cil-warning" width={26} />
                                        </strong>
                                    </CCol>
                                </CRow>
                            </CCol>
                            :
                            <CCol xs="12" lg="12">
                                {
                                    produtos_pedidos.map(produto => {
                                        const { descricao, quantity, valor, porcao_produto, sub_id, info, avulso } = produto;
                                        const name = info === 'adicionais' ? descricao : porcao_produto.nome_produto;
                                        if (!avulso && info === 'adicionais')
                                            return null;

                                        return (
                                            <CRow>
                                                <CCol xs="9" lg="8">
                                                    <Title size={'14px'}>{quantity}x {name} - {descricao}</Title>
                                                </CCol>
                                                <CCol xs="3" lg="4" style={{ justifyContent: 'flex-end', display: 'flex' }}>
                                                    <Title size={'12px'}>R$ {(valor * quantity).toFixed(2)}</Title>
                                                </CCol>
                                                {
                                                    produtos_pedidos.map((adicional, index) => {
                                                        if (sub_id && adicional.sub_id === sub_id && adicional.info === 'adicionais' && !adicional.avulso)
                                                            return (
                                                                <CCol
                                                                    xs="12"
                                                                    lg="12"
                                                                    key={index}
                                                                    style={{
                                                                        display: 'flex',
                                                                        justifyContent: 'space-between',
                                                                        paddingLeft: '25px'
                                                                    }}
                                                                >
                                                                    <Text>{adicional.quantity * quantity}x {adicional.descricao}</Text>
                                                                    <Text>R$ {(adicional.valor * adicional.quantity * quantity).toFixed(2)}</Text>
                                                                </CCol>
                                                            )
                                                    })
                                                }
                                                <CCol xs="12" lg="12">
                                                    <CRow
                                                        style={{
                                                            marginTop: '5px',
                                                            marginBottom: '10px'
                                                        }}
                                                    >
                                                        <CCol xs="2" lg="2">
                                                            <CButton color="info" onClick={() => decrement(produto)}>
                                                                <CIcon name="cil-minus" />
                                                            </CButton>
                                                        </CCol>
                                                        <CCol xs="2" lg="2">
                                                            <CButton color="info" onClick={() => increment(produto)}>
                                                                <CIcon name="cil-plus" />
                                                            </CButton>
                                                        </CCol>
                                                        <CCol
                                                            xs="8"
                                                            lg="8"
                                                            style={{
                                                                display: 'flex',
                                                                justifyContent: 'flex-end'
                                                            }}
                                                        >
                                                            <CButton color="danger" onClick={() => handleRemoveItemCart(produto)}>
                                                                <CIcon name="cil-trash" />
                                                            </CButton>
                                                        </CCol>
                                                    </CRow>
                                                </CCol>
                                            </CRow>
                                        );
                                    })
                                }
                            </CCol>
                    }
                </CRow>
            </CModalBody>
            <CModalBody style={{ borderTop: '1px solid', borderColor: '#d8dbe0' }}>
                <CRow>
                    <CCol xs="8" lg="8">
                        <Label text="SubTotal" />
                    </CCol>
                    <CCol xs="4" lg="4" style={{ display: 'flex', justifyContent: 'flex-end' }}>
                        <Label text={`R$ ${getSubTotal().toFixed(2)}`} />
                    </CCol>
                </CRow>
                <CRow>
                    <CCol xs="8" lg="8">
                        <Label text="Taxa de Entrega" />
                    </CCol>
                    <CCol xs="4" lg="4" style={{ display: 'flex', justifyContent: 'flex-end' }}>
                        <Label text={`R$ ${ taxa_de_entrega ? taxa_de_entrega.toFixed(2) : 0}`} />
                    </CCol>
                </CRow>
                <CRow>
                    <CCol xs="8" lg="8">
                        <Label text="Total" />
                    </CCol>
                    <CCol xs="4" lg="4" style={{ display: 'flex', justifyContent: 'flex-end' }}>
                        <Label text={`R$ ${getTotal().toFixed(2)}`} />
                    </CCol>
                </CRow>
            </CModalBody>
            <CModalFooter>
                <CButton
                    block
                    color="danger"
                    style={{
                        width: '100%'
                    }}
                    onClick={() => setOpen(false)}
                >
                    Fechar
                </CButton>
                {
                    produtos_pedidos.length !== 0 &&
                    <CButton
                        block
                        color="primary"
                        style={{
                            width: '100%'
                        }}
                        onClick={() => toFinishCart()}
                    >
                        Concluir Pedido
                    </CButton>
                }
            </CModalFooter>
        </CModal >
    );
};

export default ModalCartComponent;