import React from 'react';

const TitleComponent = ({ size = null, children }) => {

  return (
    <span
      style={{
        fontSize: size ? size : '14px',
        color: 'black',
        fontWeight: 'bold',
        marginBottom: '10px'
      }}
    >
      {children}
    </span>
  );
};

export default TitleComponent;
