import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';

import {
  CHeader,
  CHeaderNav
} from '@coreui/react';

import {
  TheHeaderDropdownCart,
  TheHeaderDropdownCompany
}  from './index'

const TheHeader = () => {
  const { numero } = useParams();

  const [company, setCompany] = useState(localStorage.getItem('razao_social') ?? 'Delivery Atendimento');

  useEffect(() => {
    if(numero) setCompany(localStorage.getItem('razao_social'));
  }, [numero]);  

  return (
    <CHeader withSubheader>
      <CHeaderNav className="mr-auto">
        <h4 style={{ paddingLeft: '18px', fontWeight: 'bold' }} >{company}</h4>
      </CHeaderNav>

      <CHeaderNav >
        <TheHeaderDropdownCompany />
        <TheHeaderDropdownCart />
      </CHeaderNav>
    </CHeader>
  )
}

export default TheHeader
