import React, { useState } from 'react';
import { useSelector } from 'react-redux';

import {
  CBadge,
  CDropdown,
  CDropdownToggle
} from '@coreui/react';
import CIcon from '@coreui/icons-react';

import Modal from './ModalCart';

import { valueCartProducts } from '../store/cart/selectors';

const TheHeaderDropdownTasks = () => {
  const itemsCount = useSelector(valueCartProducts);

  const [open, setOpen] = useState(false);

  return (
    <>
      <CDropdown
        inNav
        className="c-header-nav-item mx-2"
      >
        <CDropdownToggle className="c-header-nav-link" caret={false} onClick={() => setOpen(true)}>
          <CIcon name="cil-cart" size={'xl'} />
          <CBadge shape="pill" color="warning">{itemsCount}</CBadge>
        </CDropdownToggle>
      </CDropdown>
      <Modal {...{ open, setOpen }} />
    </>
  )
}

export default TheHeaderDropdownTasks;
